"use client";

import TodoForm from "@/compoments/TodoForm";
import TodoList from "@/compoments/TodoList";
import { MoonIcon, SunIcon } from "lucide-react";
import { useTheme } from "next-themes";
import Image from "next/image";
import { useState } from "react";

export default function Home() {
  const [todos, setTodos] = useState(todosData);
    const [theme = "dark", setTheme] = useTheme()


  const todosData = [
    { id: 1, text: "Learn Next.js 15", completed: false },
    { id: 2, text: "Master Node.js", completed: true },
    { id: 3, text: "Learn MongoDB", completed: true },
  ];

  const addTodo = (text) => {
    const newTodo = {
      id: Date.now().toString(),
      text,
      completed: false,
    };
    setTodos([newTodo, ...todos]);
  };

  const deleteTodo = (id) => {
    setTodos(todos.filter((todo) => todo.id !== id));
  };

  const toggleTodo = (id) => {
    setTodos(
      todos.map((todo) => {
        todo.id === id ? { ...todo, completed: !todo.completed } : todo;
      })
    );
  };

  const updateTodo = (id) => {
    setTodos(
      todos.map((todo) => (todo.id === id ? { ...todo, text: newText } : todo))
    );
  };
  return (
    <>
      <div className="min-h-screen flex flex-col  items-center justify-center py-9 px-4sm:px-6 ">
        <div className="w-full max-w-lg">
          <header className="mb-8 flex  justify-between items-center">
            <h1 className="text-3xl  font-bold  bg-clip-text text-transparent bg-gradient-to-r from-blue-400 to-purple-600">
              Add Todo
            </h1>
            <button
              onClick={() => setTheme(theme === "dark" ? "light" : "dark")}
              className="p-2 rounded-full  hover:bg-red-600 transition-colors"
              aria-label="Toggle Theme"
            >
              {theme === "dark" ? (
                <SunIcon className="h-5 w-5" />
              ) : (
                <MoonIcon className="h-5 w-5" />
              )}
            </button>
          </header>
          <TodoForm addTodo={addTodo} />
          <main className="mt-6">
            <TodoList
              todos={todos}
              deleteTodo={deleteTodo}
              updateTodo={updateTodo}
              toggleTodo={toggleTodo}
            />
          </main>
        </div>
      </div>
    </>
  );
}
